	module.exports = {"data":{"markdownRemark":{"html":"<p>到昌平新建设的老北京城缩微景点，刚刚开张。听说投资两个亿，把老北京的基本布局搞成微型城，工艺还过得去，但管理好像跟不上，有点混乱，可能因为刚刚开张。</p>\n<p>又到一个山洞里去。这不是天然的山洞，而是人工的山洞，听说原来是用于什么目的，但造好后根本没有用。里面现在放了一百多尊神仙的塑像，还有一个原始世界什么的，总的感觉也是粗糙。</p>\n<p>晚上与奇人Z一起吃饭。他做了两项表演。一是把一把叉子和一把勺子在手里轻轻抚摸，用气一吹，就成了麻花状，不仅两样东西绞在一起，每一把本身也扭转，似乎不费任何力气。然后拿了一瓶药，原装的，有腊封。他用手轻轻地扣了一下瓶子的底部，就出来很多药片，然后用嘴吹了一下，再一拍，就爆发性地出来大量药片。他要君在一张纸上写字，揉成一团，然后要先生拿住药瓶。他拿起纸团，用嘴吹一口气。纸团就到了瓶子里面。他说\"：写的是王字，连笔写的。打开腊封，到出全部药片，找到纸头，果然是那张。这真是奇迹了，不能不信。他在L背后拍了一下，象被针刺了一样跳起来。</p>\n<p>世界上有很多事情说不清楚，都说清楚了，也就不是世界了。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0136.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0136.md","frontmatter":{"title":"8月20日 星期六"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0136.md absPath of file >>> MarkdownRemark"}}

